.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  z-index: 1;
  background-color: #6f8190;
  overflow-x: hidden;
  transition: 0.5s;
}

.sidebar.show {
  width: 210px;
}

.opening {
  left: 200px;
  position: relative;
}
.menu-icon {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 24px;
  cursor: pointer;
}

.sidebar .menu-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.sidebar .menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 80%;
  height: 80px;
  margin: 10px;
  font-size: 20px;
  text-align: center;
  background-color: #f1f1f1;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  text-decoration: none;
}

.sidebar .menu-item:hover {
  transform: scale(1.1);
  background-color: #8bbacc;
}

.sidebar .menu-item.active {
  background-color: #2196f3;
  color: #ffffff;
}

.sidebar .menu-item i {
  font-size: 36px;
  margin-bottom: 5px;
}

.sidebar .menu-item i:hover {
  transform: scale(1.1);
}

.sidebar .menu-item span {
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 70%;
}

@media screen and (max-width: 768px) {
  .sidebar.sidebar.show {
    width: 100%;
  }
  
  .sidebar .menu-icon {
    display: block;
  }
  
  .sidebar .menu-container {
    margin-top: 80px;
  }
  
  .sidebar .menu-icon ,.opening {
    left: unset;
    right: 0;
    z-index: 30;
  }
  .sidebar .menu-item {
    width: 100%;
    height: 60px;
    margin: 5px;
    font-size: 16px;
  }
  
  .sidebar.show {
    width: 100%;
  }
}
