.add-product-form {
  border: 2px solid #ccc;
  padding: 20px;
  width: 80%;
  margin: 0 auto;
  text-align: left;
}

.add-product-form h2 {
  margin-top: 0;
}

.add-product-form label {
  display: block;
  margin-bottom: 10px;
}

.add-product-form select {
  width: 100%;
  height: 100px;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.add-product-form input[type="text"],
.add-product-form input[type="number"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 10px;
}

.add-product-form button {
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.add-product-form button:hover {
  background-color: #2e8b57;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 20px 0;
}

.product-card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 300px;
}

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #f7f7f7;
}

.product-name {
  font-size: 24px;
  font-weight: bold;
  margin-right: 10px;
}

.product-cost {
  font-size: 24px;
}

.product-body {
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: 100%;
}

.product-materials {
  flex: 1;
  margin-bottom: 20px;
}

.materials-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.toggle-materials {
  background-color: transparent;
  border: none;
  color: #007bff;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

.materials-info {
  display: none;
}

.product-actions {
  display: flex;
  justify-content: space-between;
}

.edit-btn,
.delete-btn {
  background-color: transparent;
  border: none;
  color: #007bff;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0 5px;
}

@media screen and (max-width: 768px) {
  .product-card {
    width: 100%;
  }
}
