@media print {
    .modal {
        position: static !important;
        transform: none !important;
        overflow: visible !important;
        display: block !important;
      }
      
      .modal-dialog {
        width: 100%;
        height: 100%;
        max-width: none;
        max-height: none;
        margin: 0;
        padding: 0;
      }
      .modal-content {
        box-shadow: none;
      }
      .modal-body {
        overflow: visible;
      }
      
    
      .modal-footer, .mqc , .menu-icon , .date-time {
        display: none !important;
      }

      
      
}