.menu-container {
  display: flex;
  flex-wrap: wrap; /* Allow menu items to wrap to next row */
  justify-content: center;
  align-items: center;
  height: auto; /* Change height to auto to fit menu items */
  margin-top: 5%;
}

.menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 40%; /* Change width to 40% to fit two items per row */
  max-width: 150px; /* Add max-width to limit item width */
  height: 120px; /* Change height to 120px to fit in small screens */
  margin: 10px; /* Reduce margin to fit more items per row */
  font-size: 4vw;
  text-align: center;
  background-color: #f1f1f1;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  text-decoration: none;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item {
    font-size: 2.5vw;
    width: 120px;
    height: 120px;
    margin: 10px;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item {
    font-size: 1.5vw;
    width: 150px;
    height: 150px;
    margin: 20px;
  }
}

@media only screen and (max-width: 768px) {
  .menu-item {
    width: 90%; /* Change width to 90% to fit one item per row */
    max-width: none; /* Remove max-width */
    height: 100px;
    margin: 5px; /* Reduce margin to fit in small screens */
    font-size: 3.5vw;
  }
}

.menu-item:hover {
  transform: scale(1.1);
  background-color: #8bbacc;
}

.menu-item.active {
  background-color: #2196f3;
  color: #ffffff;
}

.menu-item i {
  font-size: 6vw;
  margin-bottom: 5px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item i {
    font-size: 3.5vw;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item i {
    font-size: 2vw;
  }
}

.menu-item i:hover {
  transform: scale(1.1);
}

.menu-item span {
  font-size: 4vw;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 115px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .menu-item span {
    font-size: 1.2vw;
    max-width: 48px;
  }
}

@media screen and (min-width: 1024px) {
  .menu-item span {
    font-size: 1.8vw;
    max-width: 93px;
  }
}

.date-time {
  position: relative;
  width: 100%;
  padding-top: 13.25%;
}

.date-time iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 768px) {
  .date-time {
    padding-top: 22%; 
    margin-top: 10%;
  }

  .menu-item {
    height: 100px;
  }

  .menu-item i ,span {
    padding: 5px;
    
  }

  .menu-item {
    background-color: unset;

  }

  .menu-container {
    background-color: #6f8190;
  }
}
