.menu-container {
  display: flex;
  flex-wrap: wrap; /* Allow menu items to wrap to next row */
  justify-content: center;
  align-items: center;
  height: auto; /* Change height to auto to fit menu items */
  margin-top: 5%;
}

.menu-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 40%; /* Change width to 40% to fit two items per row */
  max-width: 150px; /* Add max-width to limit item width */
  height: 120px; /* Change height to 120px to fit in small screens */
  margin: 10px; /* Reduce margin to fit more items per row */
  font-size: 4vw;
  text-align: center;
